.button-border-stroke {
  height: 2rem;

  main {
    display: flex;
    justify-content: center;
    align-items: center;

    .btn {
      @green: hsl(171, 100%, 41%);
      @darkgreen: hsl(171, 100%, 31%);
      @ease-in-exponential: cubic-bezier(0.95, 0.05, 0.795, 0.035);
      @ease-out-exponential: cubic-bezier(0.19, 1, 0.22, 1);
      .ease-in {
        transition: .25s @ease-in-exponential;
      }
      .ease-out {
        transition: .65s .25s @ease-out-exponential;
      }

      position: relative;
      outline: none;
      border: 1px solid @darkgreen;
      padding: .16rem .48rem;
      background-color: transparent;
      color: @green;
      font-size: .24rem;
      line-height: 1.5;
      cursor: pointer;

      .btn-borders {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

        .border-top {
          position: absolute;
          top: 0;
          width: 100%;
          height: 1px;
          background-color: @green;
          transform: scaleX(0);
          transform-origin: left;
          .ease-in();
        }
        .border-right {
          position: absolute;
          right: 0;
          width: 1px;
          height: 100%;
          background-color: @green;
          transform: scaleY(0);
          transform-origin: bottom;
          .ease-in();
        }
        .border-bottom {
          position: absolute;
          bottom: 0;
          width: 100%;
          height: 1px;
          background-color: @green;
          transform: scaleX(0);
          transform-origin: left;
          .ease-out();
        }
        .border-left {
          position: absolute;
          left: 0;
          width: 1px;
          height: 100%;
          background-color: @green;
          transform: scaleY(0);
          transform-origin: bottom;
          .ease-out();
        }
      }

      &:hover {
        .border-top {
          transform: scaleX(1);
          .ease-out();
        }
        .border-right {
          transform: scaleY(1);
          .ease-out();
        }
        .border-bottom {
          transform: scaleX(1);
          .ease-in();
        }
        .border-left {
          transform: scaleY(1);
          .ease-in();
        }
      }

      &-text-float-up {
        .btn-text {
          position: relative;
          display: block;
          opacity: 1;
          transition: .75s .1s @ease-out-exponential;
        }

        &::after {
          content: 'Start';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          transform: translateY(30%);
          opacity: 0;
          transition: .25s ease-out;
        }

        &:hover {
          .btn-text {
            opacity: 0;
            transform: translateY(-60%);
            transition: .25s ease-out;
          }

          &::after {
            opacity: 1;
            transform: translateY(0);
            transition: .75s .1s @ease-out-exponential;
          }
        }
      }
    }
  }
}