
.landin-text {
  height: 2rem;

  main {
    display: flex;
    justify-content: center;
    align-items: center;

    p {
      display: flex;
      flex-flow: wrap;
      font-size: .24rem;
      font-weight: bold;
      font-family: Lora, serif;
      color: #fff;
      white-space: pre;
      
      span {
        animation: landIn .8s ease-out both;
      }

      @media only screen and (max-width: 419px) {
        font-size: .21rem;
      }
    }
  }
}

@keyframes landIn {
  from {
    opacity: 0;
    transform: translateY(-40%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}