.loading {
  height: 2rem;

  main {
    display: flex;
    justify-content: center;
    align-items: center;

    .dot {
      position: relative;
      width: .3rem;
      height: .3rem;
      margin: 0 .1rem;
      border-radius: 50%;
      background-color: #fff;

       &::before {
         content: '';
         position: absolute;
         width: 100%;
         height: 100%;
         border-radius: inherit;
         background-color: inherit;
         opacity: 0.3;
         animation: wave 2s ease-out infinite;
       }

       &:nth-child(1) {
         background-color: #7ef9ff;
         &::before {
           animation-delay: 0s;
         }
       }
       &:nth-child(2) {
         background-color: #89cff0;
         &::before {
           animation-delay: 0.2s;
         }
       }
       &:nth-child(3) {
         background-color: #4682b4;
         &::before {
           animation-delay: 0.4s;
         }
       }
       &:nth-child(4) {
         background-color: #0f52ba;
         &::before {
           animation-delay: 0.6s;
         }
       }
       &:nth-child(5) {
         background-color: #2c2ccf;
         &::before {
           animation-delay: 0.8s;
         }
       }
    }
  }
}

@keyframes wave {
  50%,
  75% {
    transform: scale(2.5);
  }
  80%,
  100% {
    opacity: 0;
  }
}