.float-text-menu {
  height: 3rem;

  main {
    display: flex;
    justify-content: center;
    align-items: center;

    ul {
      list-style: none;

      li {
        a {
          display: flex;
          padding: 6px;
          color: #fff;
          text-decoration: none;
          cursor: pointer;
          overflow: hidden;

          span {
            position: relative;
            transition: .3s;

            &::before {
              content: attr(data-text);
              position: absolute;
              transform: translateY(130%);
            }
          }

          &:hover {
            span {
              transform: translateY(-130%);
            }
          }
        }
      }
    }

  }

}