.header-with-slide-bar {
  height: 2rem;

  main {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;

    .slide-bar {
      position: relative;
      color: transparent;
      animation: fill-text-white 2s forwards;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transform: scaleX(0);
        transform-origin: left;
        animation: slide-in-out 2s cubic-bezier(0.75, 0, 0, 1) forwards;
      }
    }

    @keyframes slide-in-out {
      50% {
        transform: scaleX(1);
        transform-origin: left;
      }
      50.1% {
        transform-origin: right;
      }
      100% {
        transform: scaleX(0);
        transform-origin: right;
      }
    }

    @keyframes fill-text-white {
      to {
        color: #fff;
      }  
    }

    .title {
      width: 2.5rem;
      height: .3rem;
      font-size: .32rem;
      line-height: $height;
      font-family: Lora, serif;
      animation-delay: 1.6s;

      &::before {
        background-color: #FF4081;
      }
    }
    .subtitle {
      width: 2.5rem;
      height: .3rem;
      font-family: Lato, sans-serif;
      font-size: .12rem;
      line-height: $height;
      letter-spacing: 5px;
      text-transform: uppercase;
      margin-top: 10px;
      animation-delay: 3.2s;

      &::before {
        background-color: #03A9F4;
        animation-delay: 2s;
      }
    }
  }
}