.button-hover-shining {
  height: 2rem;

  main {
    display: flex;
    justify-content: center;
    align-items: center;

    .btn {
      background-color: transparent;
      outline: none;
      border: 1px solid hsl(187, 100%, 41%);
      padding: .16rem .48rem;
      font-size: .16rem;
      line-height: 1.5;
      color: #fff;
      text-transform: uppercase;
      user-select: none;
      cursor: pointer;

      &-shine {
        position: relative;
        overflow: hidden;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: linear-gradient(120deg, transparent, hsla(187, 100%, 41%, 0.5), transparent);
          transform: translateX(-100%);
          transition: .6s;
        }

        &:hover {
          box-shadow: 0 0 20px 10px hsla(187, 100%, 41%, 0.5);

          &::before {
            transform: translateX(100%);
          }
        }
      }
    }
  }
}