
.reveal-text {
  height: 2rem;

  main {
    display: flex;
    justify-content: center;
    align-items: center;

    p {
      display: flex;
      position: relative;
      font-size: .32rem;
      font-family: Raleway, sans-serif;
      color: #6ee1f5;
      letter-spacing: 3px;
      text-transform: uppercase;
      white-space: pre;

      &::before,
      &::after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        width: 2px;
        height: 100%;
        background-color: #fff;
        opacity: 0;
        transform: scale(0);
      }
      &::before {
        left: 50%;
        animation: slideLeft 1.5s cubic-bezier(0.7, -0.6, 0.3, 1.5) both;
      }
      &::after {
        right: 50%;
        animation: slideRight 1.5s cubic-bezier(0.7, -0.6, 0.3, 1.5) both;
      }

      span {
        opacity: 0;
        transform: scale(0);
        animation: fadeIn 2.4s both;
      }

      @media only screen and (max-width: 419px) {
        font-size: .25rem;
      }
    }
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes slideLeft {
  to {
    left: -5%;
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes slideRight {
  to {
    right: -5%;
    opacity: 1;
    transform: scale(1);
  }
}