.todo-list {
  height: 4rem;

  main {
    display: flex;
    justify-content: center;
    align-items: center;

    fieldset {
      display: flex;
      flex-flow: column nowrap;
      border: transparent;
      padding: 0 .75rem .1rem .3rem;
      background-color: #162740;

      legend {
        padding: 3px 6px;
        color: #f1faee;
        background-color: #264456;
      }

      label {
        display: flex;
        align-items: center;
        margin: .2rem 0;
        font-size: .18rem;
        font-family: Lato, sans-serif;
        color: #f1faee;
        cursor: pointer;

        input[type='checkbox'] {
          appearance: none;

          & + .check {
            position: absolute;
            width: .18rem;
            height: .18rem;
            border: 2px solid #f1faee;
            transition: transform .2s;
          }

          &:checked + .check {
            width: .18rem;
            height: .10rem;
            border-top: transparent;
            border-right: transparent;
            transform: rotate(-45deg)
          }

          & ~ span {
            position: relative;
            left: .36rem;
            white-space: nowrap;
            transition: color .5s;

            &::before {
              content: '';
              position: absolute;
              top: 50%;
              width: 100%;
              height: 1px;
              background-color: #f1faee;
              transform: scaleX(0);
              transform-origin: right;
              transition: transform .5s;
            }
          }

          &:checked ~ span{
            color: #585b57;

            &::before  {
              transform: scaleX(1);
              transform-origin: left;
            }
          }
        }
      }
    }
  }
}