.profile-card {
  height: 4rem;

  main {
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to top, #f3e7e9 0%, #e3eeff 99%, #e3eeff 100%);

    .card {
      @card-bg-color: hsl(240, 31%, 25%);
      @card-bg-color-transparent: hsla(240, 31%, 25%, .7);
      
      position: relative;
      width: 2.5rem;

      // 卡片四个边框
      .card-borders {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        .border-top {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 2px;
          background-color: @card-bg-color;
          transform: scaleX(0);
          transform-origin: left;
          animation: slide-in-horizontal 0.8s .6s cubic-bezier(0.645, 0.045, 0.355, 1) forwards;
        }
        .border-right {
          position: absolute;
          right: 0;
          width: 2px;
          height: 100%;
          background-color: @card-bg-color;
          transform: scaleY(0);
          transform-origin: bottom;
          animation: slide-in-vertical 0.8s .6s cubic-bezier(0.645, 0.045, 0.355, 1) forwards;
        }
        .border-bottom {
          position: absolute;
          bottom: 0;
          width: 100%;
          height: 2px;
          background-color: @card-bg-color;
          transform: scaleX(0);
          transform-origin: right;
          animation: slide-in-horizontal-reverse 0.8s .6s cubic-bezier(0.645, 0.045, 0.355, 1) forwards;
        }
        .border-left {
          position: absolute;
          left: 0;
          width: 2px;
          height: 100%;
          background-color: @card-bg-color;
          transform: scaleY(0);
          transform-origin: top;
          animation: slide-in-vertical-reverse 0.8s .6s cubic-bezier(0.645, 0.045, 0.355, 1) forwards;
        }

        // 卡片边框动画效果
        @keyframes slide-in-horizontal {
          50% {
            transform: scaleX(1);
            transform-origin: left;
          }
          50.1% {
            transform-origin: right;
          }
          100% {
            transform: scaleX(0);
            transform-origin: right;
          }
        }
        @keyframes slide-in-horizontal-reverse {
          50% {
            transform: scaleX(1);
            transform-origin: right;
          }
          50.1% {
            transform-origin: left;
          }
          100% {
            transform: scaleX(0);
            transform-origin: left;
          }
        }
        @keyframes slide-in-vertical {
          50% {
            transform: scaleY(1);
            transform-origin: bottom;
          }
          50.1% {
            transform-origin: top;
          }
          100% {
            transform: scaleY(0);
            transform-origin: top;
          }
        }
        @keyframes slide-in-vertical-reverse {
          50% {
            transform: scaleY(1);
            transform-origin: top;
          }
          50.1% {
            transform-origin: bottom;
          }
          100% {
            transform: scaleY(0);
            transform-origin: bottom;
          }
        }
      }

      // 卡片内容
      .card-content {
        display: flex;
        flex-flow: column;
        justify-content: space-evenly;
        align-items: center;
        height: 3rem;
        box-shadow: 0 0 10px rgba(0, 0, 0, .3);
        background-color: @card-bg-color-transparent;
        opacity: 0;
        transform: scale(0.6);
        animation: bump-in 0.5s 1.4s forwards;

        // 头像
        .avatar {
          width: .8rem;
          height: .8rem;
          border-radius: 50%;
          background: url('../../images/avatar.jpg') no-repeat center / contain;
          animation: bump-in 0.5s 1.6s forwards;
        }

        // 用户名
        .username {
          position: relative;
          padding: 0 2px;
          font-size: .18rem;
          letter-spacing: 2px;
          color: transparent;
          animation: fill-text-white 1.2s 2.8s forwards;
    
          // 用户名动画遮罩层
          &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #FF4081;
            transform: scaleX(0);
            transform-origin: left;
            animation: slide-in-out 1.2s 1.8s cubic-bezier(0.75, 0, 0, 1) forwards;
          }
        }
  
        // 社交图标容器
        .social-icons {
          @social-icons: 'codepen.svg', 'github.svg', 'weixin.svg';

          display: flex;
          justify-content: space-evenly;
          width: 100%;

          // 社交图标
          .social-icon {
            position: relative;
            display: block;
            width: .36rem;
            height: .36rem;
            border-radius: 50%;
            overflow: hidden;

            // 图标
            .icon {
              position: relative;
              display: block;
              width: 100%;
              height: 100%;
              border-radius: inherit;
              animation: scale-in .5s cubic-bezier(0.75, 0, 0, 1) forwards;
              transform: scale(0);
              z-index: 1;
            }

            // 图标动画效果
            &::before,
            &::after {
              position: absolute;
              content: '';
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              border-radius: inherit;
              transform: scale(0);
            }

            &::before {
              background-color: #f7f1e3;
              animation: scale-in 0.5s cubic-bezier(0.75, 0, 0, 1) forwards;
            }

            &::after {
              background-color: #2C3E50;
              animation: scale-in 0.5s cubic-bezier(0.75, 0, 0, 1) forwards;
            }
            
            .loop(@i) when (@i<=3) {
              &:nth-child(@{i}) {
                &::before {
                  animation-delay: 0.1 * @i + 3s;
                }
                &::after {
                  animation-delay: 0.1 * @i + 3.1s;
                }
                .icon {
                  @icon: extract(@social-icons, @i);
                  background: #2d3f51 url('../../images/@{icon}') no-repeat center / 60%;
                  animation-delay: 0.1 * @i + 3.2s;
                }
              }
              .loop(@i+1);
            }
            .loop(1);

          }
        }
      }

      // 卡片和头像动画
      @keyframes bump-in {
        50% {
          transform: scale(1.1);
        }
        100% {
          opacity: 1;
          transform: scale(1);
        }
      }
      // 用户名遮罩层动画
      @keyframes slide-in-out {
        50% {
          transform: scaleX(1);
          transform-origin: left;
        }
        50.1% {
          transform-origin: right;
        }
        100% {
          transform: scaleX(0);
          transform-origin: right;
        }
      }
      // 用户名文字动画
      @keyframes fill-text-white {
        to {
          color: #fff;
        }  
      }
      // 社交图标动画
      @keyframes scale-in {
        to {
          transform: scale(1);
        }
      }
    }
  }
}